import { Yaml } from '../../graphql-types';

export default function DescriptionString({
  week,
  title,
  date,
  subtitle,
  guests,
}: Partial<Yaml>): string {
  const dt = new Date(date);
  const year = dt.getFullYear();
  const month = dt.getMonth() + 1;
  const day = dt.getDate();
  return [
    ``,
    `SPITZ `,
    `${year}${month}${day}`,
    `${week}`,
    `${title}`,
    subtitle ? `${subtitle}` : ``,
    ``,
    guests.length ? `${guests.join('')}` : ``,
  ].join('');
}
